@import "../../styles/sass/variables";
@import "../../styles/sass/mixins";


.btn {
  border: none;
  @include flex(row, center, center, nowrap);
  @include text($fontFamily, 14px, 1.2, 400);
  text-transform: uppercase;
  letter-spacing: 1px;
  padding: 8px 20px;
  max-width: 80%;
  width: 30%;
  color: $textColor;
  outline: none;
  position: relative;
  z-index: 0;
  &::after {
    content: '';
    position: absolute;
    width: 30%;
    height: 8px;
    background: $primaryColor;
    bottom: 0;
    z-index: -1;
    transition: all 0.2s ease-in-out;
  }
  &:hover {
    text-decoration: none;
    color: $textColor;
    &::after{
      width: 80%;
      height: 100%;
      transition: all 0.3s ease-in-out 0s;
    }
  }

}